/* ===== PAGE BASE ===== */
.admin-body {
  padding: 30px;
  font-family: Arial, sans-serif;
}

.admin-body h3 {
  text-align: center;
  margin-bottom: 20px;
  font-weight: bold;
}

/* ===== DASHBOARD OVERVIEW ===== */
.stats {
  width: 70%;
  margin: 0 auto 35px;
  display: flex;
  justify-content: space-between;
  background: #f7eeee;
  border: 1px solid #bfbfbf;
  padding: 20px 0;
}

.stats div {
  width: 25%;
  text-align: center;
  font-weight: bold;
}

.stats div:not(:last-child) {
  border-right: 1px solid #999;
}

/* ===== ADMIN CARDS (COMMON) ===== */
.admin-card {
  width: 70%;
  margin: 0 auto 30px;
  border-radius: 4px;
  border: 1px solid #bfbfbf;
}

/* title */
.card-title {
  text-align: center;
  font-weight: bold;
  padding: 10px 0;
  border-bottom: 1px solid #bfbfbf;
}

/* content */
.card-content {
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
}

/* ===== LEFT FORM ===== */
.form-left {
  width: 65%;
}

.form-row {
  display: flex;
  align-items: center;
  margin-bottom: 14px;
}

.form-row label {
  width: 150px;
  font-weight: bold;
  font-size: 14px;
}

.form-row input {
  width: 300px;
  padding: 6px 10px;
  border-radius: 20px;
  border: 1px solid #555;
  background: #fffafa;
  outline: none;
}

/* ===== RIGHT ACTION BUTTONS ===== */
.form-actions {
  display: flex;
  flex-direction: column;
  gap: 14px;
  margin-top: 10px;
}

/* ===== BUTTON STYLES ===== */
button {
  border: none;
  cursor: pointer;
  font-size: 13px;
  padding: 8px 22px;
  border-radius: 20px;
}

.btn-edit {
  background: #7f8fb8;
  color: white;
}

.btn-delete {
  background: #e53935;
  color: white;
}

.btn-add {
  background: black;
  color: white;
}

/* ===== CARD COLORS ===== */
.careers-card {
  background: #f3f8ff;
}

.courses-card {
  background: #ffffcc;
}

.colleges-card {
  background: #e8d6ff;
}

.quiz-card {
  background: #d9ffd6;
}

/* ===== USERS SECTION ===== */
.users-card {
  background: #ffffff;
  padding: 15px 20px;
}

.users-table {
  width: 100%;
  border-collapse: collapse;
}

.users-table th,
.users-table td {
  border: 1px solid #bfbfbf;
  padding: 8px;
  text-align: center;
  font-size: 13px;
}

.users-table th {
  background: #f2f2f2;
}

.btn-view {
  background: #1e40ff;
  color: white;
  padding: 6px 18px;
  border-radius: 18px;
}
/* ================= MANAGE USERS ================= */

.users-card {
  width: 70%;
  margin: 0 auto 30px;
  background: #ffffff;
  border: 1px solid #bfbfbf;
  border-radius: 4px;
  padding: 15px 20px;
}

/* Table layout */
.users-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

/* Header cells */
.users-table th {
  background: #f2f2f2;
  font-weight: bold;
  padding: 8px;
  border: 1px solid #bfbfbf;
  font-size: 13px;
  text-align: center;
}

/* Body cells */
.users-table td {
  padding: 8px;
  border: 1px solid #bfbfbf;
  font-size: 13px;
  text-align: center;
}

/* Row hover effect */
.users-table tbody tr:hover {
  background: #f9f9f9;
}

/* View profile button */
.btn-view {
  background: #1e40ff;
  color: white;
  padding: 6px 16px;
  border-radius: 18px;
  border: none;
  font-size: 12px;
  cursor: pointer;
}

.btn-view:hover {
  background: #162fcf;
}
