.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #6fd3f7;
  padding: 10px 20px;
}

.admin-left {
  display: flex;
  align-items: center;
  gap: 12px;
}

.menu {
  font-size: 22px;
  cursor: pointer;
}

.logo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.title {
  font-weight: bold;
}

.admin-right {
  display: flex;
  align-items: center;
  gap: 15px;
}

.admin-right button {
  border: 1px solid black;
  background: white;
  padding: 5px 12px;
  cursor: pointer;
}
.menu-icon {
  cursor: pointer;       /* âœ… shows hand cursor */
  user-select: none;     /* optional: prevents text selection */
}
.logo {
  height: 40px;     /* ðŸ‘ˆ REQUIRED */
  width: auto;
  margin: 0 10px;
}
