/* ---------- Global ---------- */

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color:white;
  color: #222;
}

/* Remove default button styles */
button {
  font-family: inherit;
}

/* ---------- Shared two-column layout (Login & Register) ---------- */

.page {
  display: flex;
  min-height: 100vh;
}

/* Left illustration side */
.left-panel {
  flex: 1;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid #e0e5f0;
}

.left-panel img {
  max-width: 90%;
  height: auto;
}

/* Right form side */
.right-panel {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Card with light blue background like screenshot */
.card {
  width: 380px;
  padding: 30px 40px;
  background-color: #f3f8ff;
  border: 1px solid #cfd8e3;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.08);
}

.card h2 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 25px;
  font-size: 24px;
}

/* Labels and inputs */
.card label {
  display: block;
  margin-bottom: 5px;
  font-weight: 600;
  font-size: 14px;
}

.card input {
  width: 100%;
  padding: 8px 10px;
  margin-bottom: 15px;
  border: 1px solid #cfd8e3;
  border-radius: 3px;
  font-size: 14px;
  outline: none;
}

.card input:focus {
  border-color: #4aa3ff;
  box-shadow: 0 0 0 1px rgba(74, 163, 255, 0.3);
}

/* Small helper text and links */
.small-text {
  font-size: 13px;
  margin-bottom: 15px;
}

.small-text a {
  color: #0077ff;
  text-decoration: none;
}

.small-text a:hover {
  text-decoration: underline;
}

.center {
  text-align: center;
}

/* Primary big rounded button (Login / Create account / Start quiz) */
.primary-btn {
  display: block;
  width: 100%;
  border: none;
  color: #ffffff;
  padding: 10px 0;
  margin-top: 5px;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  background: linear-gradient(90deg, #00d26a, #5bff9b);
}

.primary-btn:hover {
  opacity: 0.9;
}

/* ---------- Home / Landing Page ---------- */

.home-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

/* Top navigation bar */
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 60px;
  background-color: #ffffff;
  border-bottom: 1px solid #e1e5ee;
}

.logo {
  font-weight: 700;
  font-size: 20px;
}

/* Right side nav links */
.top-bar nav {
  display: flex;
  gap: 15px;
}

.link-btn {
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 14px;
  color: #333;
}

.link-btn:hover {
  color: #0077ff;
}

/* Make the Register button in top bar smaller */
.top-bar .primary-btn {
  width: auto;
  padding: 8px 18px;
  font-size: 14px;
}

/* Hero / main content */
.home-content {
  flex: 1;
  text-align: center;
  padding: 60px 20px 40px;
  background-color: #ffffff;
}

.home-content h1 {
  font-size: 28px;
  margin-bottom: 15px;
}

.home-content p {
  max-width: 650px;
  margin: 0 auto 25px;
  font-size: 15px;
  line-height: 1.5;
}

/* Center Start Quiz button and make it narrower */
.home-content .primary-btn {
  max-width: 220px;
  margin: 20px auto 0;
}

/* ---------- Simple responsiveness ---------- */

@media (max-width: 900px) {
  .page {
    flex-direction: column;
  }

  .left-panel {
    border-right: none;
    border-bottom: 1px solid #e0e5f0;
    padding: 20px 0;
  }

  .card {
    margin: 30px 0;
  }

  .top-bar {
    padding: 12px 20px;
  }
}
/* ---- Why section ---- */
.why-section {
  margin-top: 40px;
}

.why-section h3 {
  margin-bottom: 15px;
  font-size: 18px;
}

.why-row {
  display: flex;
  justify-content: center;
  gap: 20px;
  max-width: 900px;
  margin: 0 auto;
}

.why-box {
  flex: 1;
  padding: 12px 16px;
  font-size: 13px;
  border-radius: 4px;
}

.why-box.green {
  background-color: #d6f6dd;
}

.why-box.pink {
  background-color: #ffd6d9;
}

/* ---- Services ---- */
.services-section {
  margin-top: 40px;
}

.services-section h3 {
  margin-bottom: 20px;
  font-size: 18px;
}

.services-row {
  display: flex;
  justify-content: center;
  gap: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.service-card {
  flex: 1;
  padding: 18px 16px 20px;
  border-radius: 4px;
  border: 1px solid #dadfe8;
  background-color: #ffffff;
  text-align: left;
}

.service-card img {
  width: 46px;
  height: 46px;
  margin-bottom: 10px;
}

.service-card h4 {
  margin: 0 0 8px;
  font-size: 15px;
}

.service-card p {
  font-size: 13px;
  line-height: 1.5;
}

.service-card.blue {
  background-color: #e5f2ff;
}

.service-card.orange {
  background-color: #ffeccd;
}

.service-card.green {
  background-color: #e4ffe0;
}

/* ---- How it works ---- */
.how-section {
  margin-top: 40px;
}

.how-section h3 {
  margin-bottom: 12px;
  font-size: 18px;
}

.how-box {
  max-width: 700px;
  margin: 0 auto;
  padding: 14px 16px;
  border-radius: 4px;
  border: 1px solid #dadfe8;
  background-color: #ffffff;
  text-align: left;
  font-size: 13px;
}

/* ---- Stats row ---- */
.stats-row {
  margin: 40px auto 0;
  max-width: 900px;
  display: flex;
  justify-content: space-around;
  font-size: 13px;
}

.stats-row strong {
  display: block;
  font-size: 18px;
}

.stats-row span {
  display: block;
  margin-top: 4px;
}
.divider {
  width: 1px;
  height: 50px;
  background-color: #888;
}

/* mobile tweak */
@media (max-width: 900px) {
  .why-row,
  .services-row,
  .stats-row {
    flex-direction: column;
    align-items: center;
  }

  .service-card,
  .why-box {
    width: 100%;
  }
}
/* top bar like screenshot */
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 70px;
  background-color: #00c4ff;
  border-bottom: 1px solid #00c4ff;
}

.logo {
  font-weight: 700;
  font-size: 18px;
  color: #ffffff;
}

.top-bar nav {
  display: flex;
  gap: 24px;
  align-items: center;
}

.link-btn {
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 14px;
  color: #ffffff;
}

/* outlined Register button on right */
.nav-register-btn {
  padding: 6px 14px;
  border-radius: 4px;
  border: 2px solid #111;
  background-color: #ffffff;
  font-size: 13px;
  font-weight: 700;
  text-decoration: none;
  color: #111;
}
.nav-register-btn:hover {
  background-color: #f0f0f0;
}

/* center cards and enlarge icons */
.services-row {
  display: flex;
  justify-content: center;
  gap: 24px;
  max-width: 1000px;
  margin: 0 auto;
}

.service-card {
  flex: 1;
  padding: 24px 20px 26px;
  border-radius: 4px;
  border: 1px solid #dadfe8;
  text-align: center;
}

/* bigger, centered icons */
.service-card img {
  width: 80px;
  height: 80px;
  margin-bottom: 14px;
  object-fit: contain;
}
.footer {
  background-color: #000;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  font-size: 14px;
}

.footer-left p,
.footer-center p,
.footer-right p {
  margin: 4px 0;
}

.footer-left {
  text-align: left;
}

.footer-center {
  text-align: center;
  flex: 1;
}

.footer-right {
  text-align: right;
  font-style: italic;
}
.role-section {
  display: flex;
  gap: 30px;
  margin: 15px 0 20px;
}

.role-section label {
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 6px;
}
.dropDownDesc{
  position: absolute;
  
  top: 5rem;
  right: 15rem;
  left: 50rem;
  width: 30%;
  padding: 20px;
  border-radius: 20px;
  background-color:white;
  border: 2px solid black;
}
.dropDownDesc ::before{
  content: '';
  position: absolute;
  top: -0.7rem;
  right: 8.3rem;
  width: 20px;
  height: 20px;
  transform: rotate(45deg);
  background-color: white;
  border-left: 2px solid black;
  border-top: 2px solid black;

}
.dropDownDescR{
  position: absolute;
  
  top: 5rem;
  right: 8rem;
  left: 56.6rem;
  width: 30%;
  padding: 20px;
  border-radius: 20px;
  background-color:white;
  border: 2px solid black;
}
.dropDownDescR ::before{
  content: '';
  position: absolute;
  top: -0.7rem;
  right: 8.3rem;
