/* ================= AFTER LOGIN HOME STYLES ================= */

.welcome-text {
  font-style: italic;
  font-weight: 600;
  margin-bottom: 10px;
}

.start-quiz-btn {
  background: #9cffc1;
  border: none;
  padding: 10px 30px;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 35px;
}
.home-content {
  text-align: center;
  padding: 40px 20px;
}
.main-title {
  font-size: 32px;
  font-weight: 800;
  margin-bottom: 15px;
}
.main-desc {
  max-width: 850px;
  margin: 0 auto 20px;
  font-size: 14px;
  line-height: 1.6;
}
.section-heading {
  text-align: left;
  max-width: 900px;
  margin: 0 auto 15px;
}
.steps-container {
  max-width: 900px;
  margin: auto;
}

.step-box {
  border: 1px solid #999;
  padding: 12px;
  margin-bottom: 10px;
  text-align: left;
}
.step-box p {
  margin: 6px 0 0;
  font-size: 14px;
}
/*----quiz-----*/
.quiz-container {
  width: 600px;
  margin: 80px auto;
  padding: 40px;
  background: #f2f2f2;
  border: 1px solid #888;
  text-align: center;
}

.quiz-container h3 {
  margin-bottom: 30px;
  font-weight: 600;
}

.radio-group {
  display: flex;
  justify-content: space-around;
  margin-bottom: 40px;
}

.radio-group label {
  font-size: 14px;
}

.continue-btn {
  padding: 6px 20px;
  border: 1px solid #444;
  background: white;
  cursor: pointer;
}
/*----10th------*/
.options-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin: 40px 0;
}

.option-btn {
  padding: 12px;
  border-radius: 25px;
  border: 1px solid #555;
  background: white;
  cursor: pointer;
}
.option-btn:hover {
  background-color: #ed9fcb;
}
.option-btn.selected {
  background: #f4b7ee;
}

.question-count {
  position: relative;
  top: -70px;   /* â¬† moves up */
  font-size: 17px;
  text-align: center;
}

.quiz-container {
  width: 900px;
  max-width: 95%;
  padding: 50px;
  background: #f2f2f2;
  border: 1px solid #888;
  text-align: center;
}
.quiz-actions {
  display: flex;
  justify-content: flex-end; /* âœ… move button to LEFT */
}
.next-btn {
  padding: 6px 20px;
  border: 1px solid #444;
  background: white;
  cursor: pointer;
}
.next-btn:hover {
  background-color: #f4acd6;
}
.finish-btn {
  background-color: #d9fdd3;   /* light green */
  border-color: #4caf50;
  color: #2e7d32;
  font-weight: 600;
}
.finish-btn:hover {
  background-color: #c8f7c5;
}
/*----profile-----*/
/* PROFILE PAGE */
.profile-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  background: #f5f5f5;
}

.profile-card {
  width: 600px;
  background: #fff5f5;
  padding: 30px 40px;
  border: 1px solid #aaa;
}

.profile-card h2 {
  text-align: center;
  margin-bottom: 25px;
}

.profile-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  font-size: 16px;
}

.label {
  font-weight: bold;
}

.profile-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.delete-btn {
  background: red;
  color: white;
  padding: 8px 18px;
  border: none;
  cursor: pointer;
}

.back-btn {
  padding: 8px 18px;
  cursor: pointer;
}

/* CONFIRM POPUP */
.confirm-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.confirm-box {
  background: white;
  padding: 25px;
  border-radius: 6px;
  width: 320px;
  text-align: center;
}

.confirm-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.confirm-delete {
  background: red;
  color: white;
  padding: 6px 15px;
  border: none;
}

.confirm-cancel {
  padding: 6px 15px;
}
